button {
  position: absolute;
  top: 0px;
  right: 0px;
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 0.75rem;
  border-style: none;
  margin: 0.5em;
  padding: 0px;
  font-size: 1rem;
  /* background-color: #e1e1e1; */
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  text-align: center;
}

button:focus,
button:hover {
  /* background-color: #1f1f1f; */
}

button:active {
  /* background-color: #000000; */
}

#darkMode button {
  border-style: none;
  font-size: 1rem;
  /* background: none;
  background-color: transparent; */
  box-shadow: rgba(233, 233, 255, 0.2) 0px 0px 10px 0px;
}

#darkMode button:focus,
#darkMode button:hover {
  box-shadow: rgba(255, 255, 255, 0.2) 0px 0px 10px 0px;
  /* background: none; */
}
#darkMode button:active {
  box-shadow: rgba(255, 255, 255, 0.2) 0px 0px 10px 0px;
  /* background: none; */
}
